#homebg, #home, #github, #myBtn {
    -webkit-animation: fadein 3s forwards; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: fadein 3s forwards; /* Firefox < 16 */
    -ms-animation: fadein 3s forwards; /* Internet Explorer */
    -o-animation: fadein 3s forwards; /* Opera < 12.1 */
    animation: fadein 3s forwards;
}

.overlay {
    position: absolute;
    left: 20%;
    z-index: 1;
    color: black;
    align-self: center;
    top: 10%; 
    font-size: 60px;
    -webkit-animation: overlaid 4.5s forwards; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: overlaid 4.5s forwards; /* Firefox < 16 */
    -ms-animation: overlaid 4.5s forwards; /* Internet Explorer */
    -o-animation: overlaid 4.5s forwards; /* Opera < 12.1 */
    animation: overlaid 4.5s forwards;
    font-family: 'Comfortaa', cursive;
}

.bulk {
    position: absolute;
    left: 20%;
    z-index: 1;
    color: black;
    align-self: center;
    top: 20%; 
    font-size: 25px;
    -webkit-animation: bulkappear 5.3s forwards; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: bulkappear 5.3s forwards; /* Firefox < 16 */
    -ms-animation: bulkappear 5.3s forwards; /* Internet Explorer */
    -o-animation: bulkappear 5.3s forwards; /* Opera < 12.1 */
    animation: bulkappear 5.3s forwards;
    font-family: 'Comfortaa', cursive;
}

#homebg {
    object-fit: cover;
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 1; }
}


