.container {
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 0;
    margin:0;
    left: 0px;
    top: 0px;
    z-index: -1000;
    overflow:hidden;
    background-color: #80d4fc;
}

.overlay {
    position: absolute;
    top: 30%; 
    left: 20%;
    z-index: 1;
}

#home {
    position: relative;
    top: 4%; 
    left: 0%;
    width: 64px;
    height: 64px;
    z-index: 1000;
}

#github {
    position: relative;
    top: 2%; 
    left: 0.2%;
    width: 60px;
    height: 60px;
    z-index: 1000;
}



.underline {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 50px;
    text-decoration: underline 0.15em rgba(0, 0, 0, 0);
    transition: text-decoration-color 400ms;
}

.underline:hover, .underline:focus {
    text-decoration-color: rgba(0, 0, 0, 1);
    text-underline-offset: 0.5em;

    -webkit-animation: underlined 0.4s forwards; /* Safari, Chrome and Opera > 12.1 */
    -moz-animation: underlined 0.4s forwards; /* Firefox < 16 */
    -ms-animation: underlined 0.4s forwards; /* Internet Explorer */
    -o-animation: underlined 0.4 forwards; /* Opera < 12.1 */
    animation: underlined 0.4s forwards;
}

/*  */
@keyframes underlined {
    0% {
        text-underline-offset: 0em;
    } 100% {
        text-underline-offset: 0.5em;
    }
}

/* Firefox < 16 */
@-moz-keyframes underlined {
    0% {
        text-underline-offset: 0em;
    } 100% {
        text-underline-offset: 0.5em;
    }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes underlined {
    0% {
        text-underline-offset: 0em;
    } 100% {
        text-underline-offset: 0.5em;
    }
}

/* Internet Explorer */
@-ms-keyframes underlined {
    0% {
        text-underline-offset: 0em;
    } 100% {
        text-underline-offset: 0.5em;
    }
}

/* Opera < 12.1 */
@-o-keyframes underlined {
    0% {
        text-underline-offset: 0em;
    } 100% {
        text-underline-offset: 0.5em;
    }
}